.Card {
  max-width: 20rem;
  display: flex;
  flex-direction: row-reverse;
  border-radius: 0.25rem;
  overflow: hidden;
  text-align: left;
  max-height: 132px;
  min-height: 132px;
  transition-duration: 0.5s;
  box-sizing: border-box;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.5); }
  .Card:hover {
    text-shadow: none;
    z-index: 999;
    max-height: 100rem;
    background: white;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.2); }
    .Card:hover .Card__Title {
      display: block;
      color: #222; }
    .Card:hover .Card__ShortDescription {
      color: #333;
      display: block; }
    .Card:hover .Card__Date {
      display: block;
      color: #666; }
  .Card__Body {
    padding: 0 0.5rem; }
  .Card__Title {
    color: #eee;
    margin: 0.25rem 0 0; }
    .Card__Title a, .Card__Title a:visited {
      color: inherit;
      text-decoration: none; }
    @media (max-width: 779px) {
      .Card__Title {
        display: none; } }
  .Card__Date {
    font-size: 0.6rem;
    color: #eee; }
    @media (max-width: 779px) {
      .Card__Date {
        display: none; } }
  .Card__ShortDescription {
    display: none;
    padding: 0.5rem 0;
    font-size: 0.8rem; }
  .Card__ImgWrapper {
    max-width: 132px;
    max-height: 132px; }
    .Card__ImgWrapper img {
      border: 2px solid #fff;
      border-radius: 0.25rem;
      max-width: 128px;
      max-height: 128px; }
